/*eslint-disable*/
import React, { PureComponent } from 'react';
import Types from 'prop-types';
import { formatWithPattern, unformatWithPattern, pushValueToStack } from '../common/textFormat';
import getPatternSymbolsWitPosition from '../common/textFormat/getPatternSymbolsWitPosition';

class InputText extends PureComponent {
  static propTypes = {
    pattern: Types.string,
    placeholder: Types.string,
  };

  static defaultProps = {
    pattern: '',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    this.state = {
      value: '',
      valueStack: [],
      isCutting: false,
      isPasting: false,
      pastedLength: 0,
    };
    this.input = React.createRef();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      const formattedValue = formatWithPattern(unFormattedValue, pattern);
      return {
        prevPattern: pattern,
        value: formattedValue,
      };
    }
    return null;
  }

  setCursorPosition = select => {
    setTimeout(() => {
      this.input.current.selectionStart = select;
      this.input.current.selectionEnd = select;
    }, 0);
  };

  handleOnKeyDown = e => {
    const { selectionStart } = this.input.current;
    this.setState({ lastKey: e.key, selectionStart });
  };

  getCursorPosition = stackWithCursor => {
    const { isCutting, isPasting, selectionStart, pastedLength } = this.state;
    const { pattern } = this.props;
    let cursorPosition = selectionStart;
    if (isPasting) {
      const symbolInSelection = getPatternSymbolsWitPosition(pattern).filter(
        symbol => symbol.index >= selectionStart && symbol.index < selectionStart + pastedLength,
      ).length;
      cursorPosition = selectionStart + symbolInSelection + pastedLength;
    } else if (!isCutting) {
      cursorPosition = stackWithCursor.indexOf('|');
    }

    return cursorPosition;
  };

  onPaste = e => {
    const pastedLength = e.clipboardData.getData('Text').length;
    this.setState({ isPasting: true, pastedLength });
  };

  handleOnChange = e => {
    const { value } = e.target;
    const { pattern } = this.props;
    const { lastKey, selectionStart } = this.state;
    const valueStack = formatWithPattern(unformatWithPattern(value, pattern), pattern).split('');

    // Handles cursor.
    const stackWithCursor = pushValueToStack(valueStack, lastKey, selectionStart, pattern);
    this.setCursorPosition(this.getCursorPosition(stackWithCursor));

    const newStack = stackWithCursor.filter(char => char !== '|');
    const newValue = formatWithPattern(unformatWithPattern(newStack.join(''), pattern), pattern);

    this.setState({ value: newValue }, this.resetEvents());
  };

  getFormattedValue = newStackWithCursor => {
    const { pattern } = this.props;
    const newStack = newStackWithCursor.filter(char => char !== '|');
    return formatWithPattern(newStack.join(''), pattern);
  };

  resetEvents = () => {
    this.setState({
      isCutting: false,
      isPasting: false,
      pastedLength: 0,
    });
  };

  render() {
    const { value } = this.state;
    const { placeholder } = this.props;

    return (
      <input
        type="text"
        value={value}
        ref={this.input}
        className="form-control"
        onKeyDown={e => this.handleOnKeyDown(e)}
        onChange={e => this.handleOnChange(e)}
        placeholder={placeholder}
        onCut={() => this.setState({ isCutting: true })}
        onPaste={e => this.onPaste(e)}
      />
    );
  }
}

export default InputText;



