/*eslint-disable */
import getPatternSymbolsWitPosition from '../getPatternSymbolsWitPosition';
const pushValueToStack = (valueStack, key, selectionStart, pattern) => {
  switch (key) {
    case 'Delete':
      valueStack.splice(selectionStart, 0, '|');
      break;
    case 'Backspace':
      const isMultipleDeletion = false;
      if (isMultipleDeletion) {
      } else {
        // We need to consider that a char has been deleted with selectionStart - 1
        // Also we don't need to delete anything here as onChange value has been already deleted
        valueStack.splice(selectionStart - 1, 0, '|');
      }
      break;
    default:
      const symbolInSelection = getPatternSymbolsWitPosition(pattern).filter(
        symbol => symbol.index >= selectionStart && symbol.index < selectionStart + 1,
      ).length;

      valueStack.splice(selectionStart + symbolInSelection + 1, 0, '|');
      console.log('valueStackAfter', valueStack);
  }
  return valueStack;
};

export default pushValueToStack;



